<template>
  <q-page-container id="login">
    <div class="login__content page">
      <div class="login__topArea">
        <img src="@/assets/login/login_top_img.png" alt="login background" />
      </div>
      <div class="login__inputArea">
        <div class="login__inputArea__slogan">
          <p>Hello,</p>
          <p>Welcome!</p>
        </div>
        <div class="login__input">
          <div class="row">
            <div class="col-2 login__input__label">
              
            </div>
            <div class="col-9 offset-1">
              <q-input v-model="account" borderless class="login__input--input" placeholder="" :rules="[validatePhoneNumber]" maxlength="10" />
            </div>
          </div>
        </div>
        <div class="login__input">
          <div class="row">
            <div class="col-2 login__input__label">
              
            </div>
            <div class="col-9 offset-1">
              <q-input v-model="password" borderless type="password" class="login__input--input" placeholder="**********" :rules="[validatePassword]" maxlength="20" />
            </div>
          </div>
        </div>
        <div class="row justify-end">
          <div class="cursor-pointer">
            ?
          </div>
        </div>

        <div class="row justify-center login_btnArea">
          <div class="login__btnArea--btn" @click="onLogin">
            
          </div>
        </div>
      </div>
    </div>
  </q-page-container>
</template>

<script setup lang="ts">
import { useRouter } from 'vue-router'
import { useQuasar } from 'quasar'

const $q = useQuasar()
const account = ref<string>('')
const password = ref<string>('')
const router = useRouter()

/**
 * 
 * 
 */
const validatePhoneNumber = (val: string): boolean | string => {
  const regex = /^09\d{8}$/
  return regex.test(val) || ''
}

/**
 * 
 *  12 
 */
const validatePassword = (val: string): boolean | string => {
  const regex = /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[A-Za-z\d]{12,}$/
  return regex.test(val) || ''
}

/**
 * 
 *  localStorage
 */
const onLogin = async (): Promise<void> => {
  try {
    // 
    if (!account.value || !password.value) {
      $q.notify({
        type: 'warning',
        message: '',
        position: 'top',
        timeout: 2000
      })
      throw new Error('')
    }

    //  localStorage  loginData 
    const loginData = {
      account: account.value,
      password: password.value
    }
    localStorage.setItem('loginData', JSON.stringify(loginData))

    // 
    await router.push('/')
  } catch (error) {
    //  Quasar 
    console.error(':', error)
  }
}
</script>

